
.page-tech {
  padding-top: 140px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  @include r(768) {
    padding-top: 100px; }

  .breadcrumbs__container {
    li {
      color: #000; }
    a {
      color: #000; } }

  .col-md-6 {
    padding-left: 0;
    padding-right: 20px;
    display: flex;
    align-items: center;
    @include r(580) {
      padding-right: 10px; } } }

.page-tech-wrapper {
  padding-left: 40px;
  padding-right: 20px;
  padding-bottom: 100px;
  padding-top: 0px;
  width: 100%;
  height: 110%;
  @include r(768) {
    padding-left: 25px;
    padding-bottom: 20px;
    padding-right: 5px;
    @include r(580) {
      padding-bottom: 40px;
      padding-left: 20px;
      padding-right: 5px; } } }

.head__page-tech-title {
  h1 {
    color: #000;
    width: 100%;
    max-width: 630px;
    padding-top: 75px;
    padding-bottom: 95px;
    @include r(768) {
      max-width: 450px;
      padding-bottom: 60px;
      @include r(380) {
        font-size: 3rem; } } } }

.tech-content-wrap {
  max-width: 440px;
  @include r(1070) {
    max-width: 370px;
    padding-right: 20px;
    @include r(768) {
      margin: 0 auto;
      max-width: 400px;
      margin-bottom: 40px;
      @include r(580) {
        margin-bottom: 20px; } } } }

.tech-content-title {
  @include fontSR;
  font-size: 2.4rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  padding-top: 25px;
  padding-bottom: 5px; }

.tech-content-subtitle {
  @include fontFR;
  font-size: 1.8rem;
  line-height: 150%;
  letter-spacing: 0.1em;
  color: #000000; }

.tech-img-wrap {
  background-color: #EBEBEB;
  width: 100%;
  max-width: 100%;
  position: relative;
  height: 100%;

  img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover; }

  .tech-img-wrap-icons {
    position: absolute;
    top: 30px;
    left: 30px;
    @include r(580) {
      top: 15px;
      left: 15px;
      max-width: 60px; } } }

.icons-right {
  left: auto !important;
  right: 30px !important;
  max-width: 80px; }

// half

.tech-content-half-wrap {
  width: 100%;
  max-width: 100%;
  margin-bottom: 50px; }

.tech-content-half-img {
  position: relative;
  margin-bottom: 35px;
  background-color: #EBEBEB;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    max-height: 390px;
    @include r(768) {
      max-height: 100%; } } }

.tech-content-half-icons {
  position: absolute;
  top: 30px;
  left: 30px; }

.tech-content-half-title {
  @include fontSR;
  font-size: 2.4rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 5px;
  @include r(768) {
    text-align: center;
    font-size: 3rem;
    @include r(420) {
      font-size: 2.4rem; } } }

.tech-content-half-subtitle {
  @include fontFR;
  font-size: 1.8rem;
  line-height: 150%;
  letter-spacing: 0.1em;
  color: #000000;
  max-width: 470px;
  @include r(1070) {
    max-width: 370px;
    padding-right: 20px;
    @include r(768) {
      margin: 0 auto;
      padding-right: 0;
      font-size: 2rem;
      max-width: 450px;
      @include r(420) {
        font-size: 1.8rem; } } } }


.margin {
  margin-top: 150px;
  margin-bottom: 150px;
  height: 1px;
  width: 100%; }

.col-row {
  margin-bottom: 120px;
  @include r(768) {
    margin-bottom: 30px; } }
